form {
  display: flex;
  justify-content: center;
  gap: 10px;
}

input {
  width: 300px;
  line-height: 1.5;
  border-radius: 3px;
  border: 2px solid #ffffff17;
}

button {
  padding: 5px;
  border: 2px solid transparent;
  border-radius: 3px;

  @include transition-double(transform, background-color);
  transform: scale(1);

  &[type='submit'] {
    background-color: #bbd5ff;
  }

  &:hover {
    background-color: #a3beeb;
    transform: scale(0.98);
  }
}
